    // 舆情分析，初始化河南省行政区图 百度地图加echart
    initBMapChart(){
        let myChart = echarts.init(this.$refs.map);
        window.addEventListener('resize', function () {
            myChart.resize()
        })
        var that = this
        //渲染函数
        function renderItem(params, api) {
            var coords = that.zhengzhou_json;
            var points = [];
            for (var i = 0; i < coords.length; i++) {
                points.push(api.coord(coords[i]));
            }
            var color = api.visual('color');

            return {
                type: 'polygon',
                shape: {
                    points: echarts.graphic.clipPointsByRect(points, {
                        x: params.coordSys.x,
                        y: params.coordSys.y,
                        smooth:0,
                        width: params.coordSys.width,
                        height: params.coordSys.height
                    })
                },
                style: api.style({
                    fill: 'green',
                    lineWidth:2,
                    stroke: 'rgb(43, 123, 56)'
                })
            };
        }
        myChart.setOption({
            bmap: { //设置百度地图显示哪些东西
                center: [113.933282,33.841431],
                zoom:8,
                roam:true,
