<template>
  <div class="outer">
    <div class="t-menu">
        <div class="menu-top">
            <div class="top-item" :style="style1" @click="click1">潜在风险社区识别</div>
            <div class="top-item" :style="style2" @click="click2">病例出行链展示</div>
            <div class="top-item" :style="style3" @click="click3">疾病传播路径研判</div>
            <div class="top-item" :style="style4" @click="click4">疫情空间维度舆情监测</div>
        </div>
        <div class="menu-bottom" :style="activeStyle"></div>
    </div>
    <div class="content">

      <!-- 地图容器 -->
      <div id="container" ref="map"></div>
      <!-- <div class="leftpoint"> -->
        
      <!-- </div> -->
      <div class="right-content">
        <div class="r-top">
            <div class="r-title">
                病例出行连展示
            </div>
            <div class="r-form">
                <div class="form-left">市县选择：</div>
                <!-- <input class="form-right" type="date" /> -->
                <select class="form-right" v-model="cityValue" @change="changeTrace">
                  <option>郑州市</option>
                  <option>洛阳市</option>
                  <option>信阳市</option>
                  <option>南阳市</option>
                  <option>驻马店市</option>
                  <option>开封市</option>
                  <option>新乡市</option>
                  <option>焦作市</option>
                  <option>濮阳市</option>
                  <option>三门峡市</option>
                  <option>平顶山市</option>
                  <option>商丘市</option>
                  <option>安阳市</option>
                  <option>鹤壁市</option>
                  <option>济源市</option>
                </select>
            </div>
            <!-- <div class="r-form"> -->
                <!-- <div class="form-left">目标开始时间：</div> -->
                <!-- <input class="form-right" type="date" /> -->
            <!-- </div> -->
            <!-- <div class="r-form"> -->
                <!-- <div class="form-left">目标开始时间：</div> -->
                <!-- <input class="form-right" type="date" /> -->
            <!-- </div> -->

        </div>
        
        <!-- 分析结果 -->
        <!-- <div class="result"> -->
            <!-- <div class="r-title">关键词:</div> -->
            <!-- <div class="result-content"> -->
                <!-- <img src="../assets/keywords/keyword1.png" alt="" /> -->
            <!-- </div> -->
            
        <!-- </div> -->
        
        <!-- echarts表格 -->
        <!-- <div class="for-charts"> -->
            <!-- <div class="r-title">对比图</div> -->
            <!-- <div class="myCharts" ref="mycharts"></div> -->
        <!-- </div> -->
      </div>
    </div>
  </div>
</template>

<script>
const echarts = require('echarts');

import 'echarts/extension/bmap/bmap'
import axios from 'axios'

export default {
  data() {
    return {
      style1: '',
      style2: 'color:rgb(255, 208, 75);',
      style3: '',
      style4: '',
      activeStyle: 'transform: translate3d(16.75vw, 0px, 0px);',
      zhengzhou_center:[113.666165,34.752875],
      luoyang_center:[112.43831882437327, 34.684984113189614],
      zhumadian_center:[114.054443,32.985358],
      xinyang_center:[114.645456,32.113229],
      nanyang_center:[112.607955,33.02363],
      nomal_zoom:10,
      xinyang_zoom:9,
      cityValue:'郑州市',
      xinyang_json:[],
      nanyang_json1:[],
      zhumadian_json:[],
      zhengzhou_json:[],
      luoyang_json:[],
    };
  },
  mounted() {
    //this.initBMap()
    //this.initHeNan()
    //this.initTraceCharts()
    //this.initBMapChart()
    //this.initCharts()
  },
  created(){
    //this.get_nanyang()
    //this.get_zhumadian()
    this.get_zhengzhou()
  },
  methods: {
    changeTrace(){
        switch (this.cityValue){
            case '郑州市':
                this.get_zhengzhou()
                break;
            case '驻马店市':
                this.get_zhumadian()
                break;
            case '南阳市':
                this.get_nanyang()
                break;
            case '信阳市':
                this.get_xinyang()
                break;
            case '洛阳市':
                this.get_luoyang()
                break;
            
        }
    },
    click1(){
        this.activeStyle = "transform: translate3d(0vw, 0px, 0px);"
        this.style1 = 'color:rgb(255, 208, 75);'
        this.style2 = ''
        this.style3 = ''
        this.style4 = ''
        this.$router.push('/community')
    },
    click2(){
        this.activeStyle = ""
        this.style1 = ''
        this.style2 = 'color:rgb(255, 208, 75);'
        this.style3 = ''
        this.style4 = ''
    },
    click3(){
        this.activeStyle = "transform: translate3d(33.5vw, 0px, 0px);"
        this.style1 = ''
        this.style2 = ''
        this.style3 = 'color:rgb(255, 208, 75);'
        this.style4 = ''
        //this.$router.push('/community')
    },
    click4(){
        this.activeStyle = "transform: translate3d(50.25vw, 0px, 0px);"
        this.style1 = ''
        this.style2 = ''
        this.style3 = ''
        this.style4 = 'color:rgb(255, 208, 75);'
        this.$router.push('/')
    },
    // 获取南阳json
    get_nanyang(){
        var that = this
        axios.get('static/api/nanyang_patient_simple.json').then(function(res){
                var ny = res.data
                var hStep = 300 / (ny.length - 1);
                for (var i = 0; i<ny.length;i++){
                    that.nanyang_json1.push(
                        {
                            coords: ny[i],
                            lineStyle: {
                                normal:{
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(ny[i].length)*10)
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(hStep * i))
                                    color: 'rgb('+Math.random()*255+','+Math.random()*90+','+Math.random()*90+')'
                                    //color: 'red'
                                }
                            }
                            
                        }
                    )
                }
                that.initTraceCharts(that.nanyang_json1,that.nanyang_center,that.nomal_zoom)
            
            }
        
        
        )
    
    },
    // 信阳市
    get_xinyang(){
        var that = this
        axios.get('static/api/xinyang_patient_simple.json').then(function(res){
                var ny = res.data
                var hStep = 300 / (ny.length - 1);
                for (var i = 0; i<ny.length;i++){
                    that.xinyang_json.push(
                        {
                            coords: ny[i],
                            lineStyle: {
                                normal:{
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(ny[i].length)*10)
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(hStep * i))
                                    color: 'rgb('+Math.random()*255+','+Math.random()*90+','+Math.random()*90+')'
                                    //color: 'red'
                                }
                            }
                            
                        }
                    )
                }
                console.log(that.xinyang_json.length)
                that.initTraceCharts(that.xinyang_json,that.xinyang_center,that.xinyang_zoom)
            
            }
        
        
        )
    
    },
    
    // 驻马店市
    get_zhumadian(){
        var that = this
        axios.get('static/api/zhumadian_patient_simple.json').then(function(res){
                var ny = res.data
                var hStep = 300 / (ny.length - 1);
                for (var i = 0; i<ny.length;i++){
                    that.zhumadian_json.push(
                        {
                            coords: ny[i],
                            lineStyle: {
                                normal:{
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(ny[i].length)*10)
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(hStep * i))
                                    color: 'rgb('+Math.random()*255+','+Math.random()*90+','+Math.random()*90+')'
                                    //color: 'red'
                                }
                            }
                            
                        }
                    )
                }
                console.log(that.zhumadian_json.length)
                that.initTraceCharts(that.zhumadian_json,that.zhumadian_center,that.nomal_zoom)
            
            }
        
        
        )
    
    },
    
    // 郑州市
    get_zhengzhou(){
        var that = this
        axios.get('static/api/zhengzhou_patient_simple.json').then(function(res){
                var ny = res.data
                var hStep = 300 / (ny.length - 1);
                for (var i = 0; i<ny.length;i++){
                    that.zhengzhou_json.push(
                        {
                            coords: ny[i],
                            lineStyle: {
                                normal:{
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(ny[i].length)*10)
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(hStep * i))
                                    color: 'rgb('+Math.random()*255+','+Math.random()*90+','+Math.random()*90+')'
                                    //color: 'red'
                                }
                            }
                            
                        }
                    )
                }
                console.log(that.zhengzhou_json.length)
                that.initTraceCharts(that.zhengzhou_json,that.zhengzhou_center,that.nomal_zoom)
            
            }
        
        
        )
    
    },
    
        // 洛阳市
    get_luoyang(){
        var that = this
        axios.get('static/api/luoyang_patient_simple.json').then(function(res){
                var ny = res.data
                var hStep = 300 / (ny.length - 1);
                for (var i = 0; i<ny.length;i++){
                    that.luoyang_json.push(
                        {
                            coords: ny[i],
                            lineStyle: {
                                normal:{
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(ny[i].length)*10)
                                    //color: echarts.color.modifyHSL('#5A94DF', Math.round(hStep * i))
                                    //color: 'rgb('+Math.random()*255+','+Math.random()*90+','+Math.random()*90+')'
                                    color: 'red'
                                }
                            }
                            
                        }
                    )
                }
                console.log(that.luoyang_json.length)
                that.initTraceCharts(that.luoyang_json,that.luoyang_center,that.nomal_zoom)
            
            }
        
        
        )
    
    },
    
    // 轨迹图
    initTraceCharts(data,center,zoom){
        let myChart = echarts.init(this.$refs.map);
        //窗口变动监听
        window.addEventListener('resize', function () {
            myChart.resize()
        })
        var d = data
        console.log(d.length)
        myChart.setOption({
            bmap: {
                center: center,
                //center: [113.662006,34.749485],
                zoom: zoom,
                roam: true,
                mapStyle: {
                  'styleJson': [
                    {
                      'featureType': 'water',
                      'elementType': 'all',
                      'stylers': {
                        //'color': '#2d78f4'
                        'color': '#031628'
                      }
                    },
                    {
                      'featureType': 'land',
                      'elementType': 'geometry',
                      'stylers': {
                       'color': '#000102'
                       //'color': '#ffffef'
                      }
                    },
                    {
                      'featureType': 'highway',
                      'elementType': 'all',
                      'stylers': {
                        'visibility': 'off'
                      }
                    },
                    {
                      'featureType': 'arterial',
                      'elementType': 'geometry.fill',
                      'stylers': {
                        'color': '#000000'
                      }
                    },
                    {
                      'featureType': 'arterial',
                      'elementType': 'geometry.stroke',
                      'stylers': {
                        'color': '#0b3d51'
                      }
                    },
                    {
                      'featureType': 'local',
                      'elementType': 'geometry',
                      'stylers': {
                        'color': '#000000'
                      }
                    },
                    {
                      'featureType': 'railway',
                      'elementType': 'geometry.fill',
                      'stylers': {
                        'color': '#000000'
                      }
                    },
                    {
                      'featureType': 'railway',
                      'elementType': 'geometry.stroke',
                      'stylers': {
                        'color': '#08304b'
                      }
                    },
                    {
                      'featureType': 'subway',
                      'elementType': 'geometry',
                      'stylers': {
                        'lightness': -70
                      }
                    },
                    {
                      'featureType': 'building',
                      'elementType': 'geometry.fill',
                      'stylers': {
                        'color': '#000000'
                      }
                    },
                    {
                      'featureType': 'all',
                      'elementType': 'labels.text.fill',
                      'stylers': {
                        'color': '#857f7f'
                      }
                    },
                    {
                      'featureType': 'all',
                      'elementType': 'labels.text.stroke',
                      'stylers': {
                        'color': '#000000'
                      }
                    },
                    {
                      'featureType': 'building',
                      'elementType': 'geometry',
                      'stylers': {
                        'color': '#022338'
                      }
                    },
                    {
                      'featureType': 'green',
                      'elementType': 'geometry',
                      'stylers': {
                        'color': '#062032'
                      }
                    },
                    {
                      'featureType': 'boundary',
                      'elementType': 'all',
                      'stylers': {
                        'color': '#465b6c'
                      }
                    },
                    {
                      'featureType': 'manmade',
                      'elementType': 'all',
                      'stylers': {
                        'color': '#022338'
                      }
                    },
                    {
                      'featureType': 'label',
                      'elementType': 'all',
                      'stylers': {
                        'visibility': 'off'
                      }
                    }
                  ]
                }
            },
            series: [{
                type: 'lines',
                coordinateSystem: 'bmap',
                polyline: true,
                data: d,
                silent: true,
                lineStyle: {
                    // color: '#c23531',
                    // color: 'rgb(200, 35, 45)',
                    opacity: 0.4,
                    width: 1
                },
                progressiveThreshold: 500,
                progressive: 200
            }, {
                type: 'lines',
                coordinateSystem: 'bmap',
                polyline: true,
                //data: busLines,
                data: d,
                lineStyle: {
                    width: 0
                },
                effect: {
                    constantSpeed: 20,
                    show: true,
                    trailLength: 0.1,
                    symbolSize: 1.5
                },
                zlevel: 1
            }]
        })
        var bmap = myChart.getModel().getComponent('bmap').getBMap();
        bmap.addControl(new BMap.MapTypeControl());
    }
  }
};
</script>

<style lang="stylus" scoped>
.outer {
  background: #fff;
  height: 100vh;
  //display: flex;
  //flex-direction: column;
  .content {
    display: flex;
    //flex-direction: row;
    //flex: 1;
  }
  .t-menu{
    width 100%
    height 10vh
    background-color rgb(84, 92, 100)
    .menu-top{
        color white
        height 10vh
        line-height 9.6vh
        width 67%
        margin-left 8%
        //background red
        //display flex
        .top-item{
            width 25%
            height 100%
            text-align center
            float left
        }
        .top-item:hover{
            //background-color rgb(67, 74, 80)
            background red
        }
    }
    .menu-bottom{
        width 16.75%
        height 0.4vh
        margin-left 8%
        margin-top -0.4vh
        transition transform 0.25s cubic-bezier(0.455, 0.03, 0.515, 0.955)
        background rgb(255, 208, 75)
    }
  }
  .el-menu-demo {
    padding: 0 110px;
    li {
      width: 20%;
      text-align: center;
    }
  }
  .el-row {
    .el-col {
      height: 100%;
      ul {
        height: 100%;
      }
    }
  }
  #container {
    width: 65%;
    height: 90vh;
    background darkgray
  }
  .right-content{
    width: 35%
    height: 90vh
    background: #eae8e8
    overflow scroll
    .r-top{
        box-sizing boder-box
        padding-left 10px
        .r-title{
            //background yellow
            width 100%
            height 50px
            font-size 13px
            font-weight bold
            padding-top 10px
            line-height 50px
        }
        .r-form{
            width 90%
            margin auto
            //background green
            height 40px
            line-height 40px
            display flex
            .form-left{
                width 40%
                font-size 10px
                
            }
            .form-right{
                height 30px
                margin-top 5px
                //text-align center
                letter-spacing 4px
                width 60%
            }
        }
    }
    .result{
        width 100%
        //height 200px
        .r-title{
            //background yellow
            width 50%
            height 50px
            font-size 13px
            font-weight bold
            padding-top 10px
            margin-left 15px
            line-height 50px
        }
        .result-content{
            //border 1px solid darkgreen
            width 80%
            //height 70%
            margin auto
            img {
                width 100%
            }
        }
        
    }
    .for-charts{
        width 100%
        height 300px
        margin-bottom 100px
        .r-title{
            //background yellow
            width 50%
            height 50px
            font-size 13px
            font-weight bold
            padding-top 10px
            margin-left 15px
            line-height 50px
        }
        .myCharts{
            width 100%
            height 90%
            //background red
            margin auto
        }
    }
  }
}
.anchorBL{
    display:none;
} 
</style>


