import Vue from 'vue'
import Router from 'vue-router'

import index from '@/components/index'         //舆情
import forIndex from '@/components/forIndex'   //五张表
import trace from '@/components/trace'         //出行链
import community from '@/components/community' //风险社区

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'index',
      component: index
    },{
      path: '/forIndex',
      name: 'forIndex',
      component: forIndex
    },{
      path: '/community',
      name: 'community',
      component: community
    },{
      path: '/trace',
      name: 'trace',
      component: trace
    }
  ]
})



