import 'echarts/map/js/province/henan.js'
import 'echarts/map/js/world.js'
import axios from 'axios'

export default {
  data() {
    return {
      //activeIndex: "1",
      //activeIndex2: "1",
      style1: 'color:rgb(255, 208, 75);',
      style2: '',
      style3: '',
